<template>
  <div class="right-content" :key="instanceKey">
    <div class="top-section section-box">
      <div class="section-bg1">
        <SubtitleBar :subtitle="`计划完成`">
          <a-select
            ref="select"
            label-in-value
            v-model:value="searchInfo.selectPlannedValue"
            :options="optionsDimension"
            style="width: 1.2rem"
            @change="handlePlannedChange"
            v-show="optionsDimension.length > 1"
          />
        </SubtitleBar>
        <IndicatorGroup
          class="indicator-group-layout"
          :data="plannedData"
          :style="
            tabStatus === 'on'
              ? 'width: 80%; margin: -0.07rem 7.5% 0 10%'
              : 'width: 80%; margin: -0.15rem 7.5% 0 10%'
          "
          :class="tabStatus === 'on' ? '' : 'fix-indicator-group-padding'"
          @click-item="handlePlanPower"
        />
        <div style="width: 100%; height: 1.85rem">
          <div style="margin-left: 0.16rem; transform: scale(0.85); transform-origin: 0 0">
            <EchartLineBarChart
              :data="plannedChartData"
              :colors="plannedChartData.colors"
              :width="`26.14vw`"
              :height="tabStatus === 'on' ? `23.94vh` : `26.83vh`"
              :category="plannedChartData.categories"
              ybgcolor="#01B7D730"
              :name="plannedChartData.seriesName"
              :rotate="0"
              @clickItem="handlePlanPowerByParams"
              :dataZoomDisabled="false"
              :dataZoomMaxValueSpan="plannedChartData.categories.length"
              :dataZoomDefaultValueRange="[0, 4]"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="middel-section section-box">
      <div class="section-bg2">
        <SubtitleBar :subtitle="`厂用电`">
          <a-select
            ref="select"
            label-in-value
            v-model:value="searchInfo.selectFactroyValue"
            :options="optionsDimension"
            style="width: 1.2rem"
            @change="handleFactroyChange"
            v-show="optionsDimension.length > 1"
          />
        </SubtitleBar>
        <IndicatorGroup
          class="indicator-group-layout"
          :data="factroyData"
          :style="
            tabStatus === 'on'
              ? 'width: 62%; margin-top: -0.07rem; margin-left: 20%'
              : 'width: 62%; margin-top: -0.15rem; margin-left: 20%'
          "
          :class="tabStatus === 'on' ? '' : 'fix-indicator-group-padding'"
          @click-item="handlePowerContrast"
        />
        <div style="width: 100%; height: 1.85rem">
          <div style="margin-left: 0.16rem; transform: scale(0.85); transform-origin: 0 0">
            <EchartCrossBarChart
              :data="factroyChartData"
              :colors="factroyChartData.colors"
              :names="factroyChartData.names"
              :width="`26.14vw`"
              :height="tabStatus === 'on' ? `23.94vh` : `26.83vh`"
              :category="factroyChartData.categories"
              ybgcolor="#01B7D730"
              :rotate="0"
              @clickItem="handlePowerContrastByParams"
              :dataZoomDisabled="false"
              :dataZoomMaxValueSpan="factroyChartData.categories.length"
              :dataZoomDefaultValueRange="[0, 4]"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="bottom-section section-box">
      <div class="section-bg1">
        <SubtitleBar :subtitle="`储能电量`">
          <a-select
            ref="select"
            label-in-value
            v-model:value="searchInfo.selectChargingValue"
            :options="optionsDimension"
            style="width: 1.2rem"
            v-show="optionsDimension.length > 1"
          />
        </SubtitleBar>
        <IndicatorGroup
          class="indicator-group-layout"
          :data="lurkingData"
          :style="
            tabStatus === 'on'
              ? 'width: 62%; margin: -0.07rem 15% 0 20%'
              : 'width: 62%; margin: -0.15rem 15% 0 20%'
          "
          :class="tabStatus === 'on' ? '' : 'fix-indicator-group-padding'"
        />
        <div style="width: 100%; height: 1.85rem">
          <div style="margin-left: 0.16rem; transform: scale(0.85); transform-origin: 0 0">
            <EchartLineBarChart
              :data="sbarchartData"
              :colors="sbarchartData.colors"
              :width="`26.14vw`"
              :height="tabStatus === 'on' ? `23.94vh` : `26.83vh`"
              :category="sbarchartData.categories"
              ybgcolor="#01B7D730"
              :name="sbarchartSeriesName"
              :rotate="0"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
  import { reactive, UnwrapRef, ref, onMounted, watch } from 'vue';
  import SubtitleBar from '/@/components/Framework/Chart/SubtitleBar.vue';
  import IndicatorGroup from '/@/components/Framework/Chart/IndicatorGroup.vue';
  import EchartLineBarChart from '/@/components/Framework/Chart/EchartLineBarChart.vue';
  import EchartCrossBarChart from '/@/components/Framework/Chart/EchartCrossBarChart.vue';
  import { getPlannedPowerData, getFactroyUsedPowerData } from './data';
  import * as CommonApi from '@/api/da/common';
  import { assign } from 'lodash-es';
  import { handleRouteChange } from '../../utils';
  import { buildUUID } from '@/utils/uuid';

  //搜索参数
  const searchInfo: UnwrapRef<any> = reactive({
    //计划电量
    selectPlannedValue: 'station',
    //厂用电
    selectFactroyValue: 'station',
    // 储能电量
    selectChargingValue: 'station',
  });
  //父传子值
  const indexData: UnwrapRef<any> = reactive({});
  const instanceKey = ref(buildUUID());

  interface Props {
    topIndexData?: {};
    searchInfoByTime?: {};
    tabStatus?: '';
  }
  const props = withDefaults(defineProps<Props>(), {});

  const optionsDimension: any = ref([]);

  watch(
    () => props.searchInfoByTime,
    (value) => {
      assign(searchInfo, value);
      queryPlannedPowerData();
      queryFactroyPowerData();
    },
  );

  watch(
    () => props.topIndexData,
    (value) => {
      assign(indexData, value);
      setValueByTopIndex();
    },
  );

  onMounted(async () => {
    const sts = await CommonApi.getDictDataMap({ dictType: 'cockpit_three_dimension' });
    sts.result.forEach((e) => {
      optionsDimension.value.push({ value: e.value, label: e.label });
    });
    assign(indexData, props.topIndexData);
    assign(searchInfo, props.searchInfoByTime);
    setValueByTopIndex();
  });

  //=============================== 计划完成==============================================
  const plannedData = ref({});
  const plannedChartData = ref({
    barData: [[], []],
    lineData: [[]],
    colors: [
      ['#488FF6', '#60D1F3'],
      ['#078C5D', '#68E4B8'],
      ['#E59837', '#FAE895'],
      ['#e59837', '#fae895'],
      ['#078C5D', '#68E4B8'],
    ],
    categories: [],
    seriesName: [['减线损上网电量', '计划电量'], ['完成率']],
  });
  const queryPlannedPowerData = async () => {
    searchInfo.dimension = searchInfo.selectPlannedValue;
    let data = await getPlannedPowerData(searchInfo);
    if (data.result) {
      if (data.result.lineData.length) {
        plannedChartData.value.lineData[0] = data.result.lineData;
      } else {
        plannedChartData.value.lineData = [];
      }
      plannedChartData.value.barData = data.result.barData;
      plannedChartData.value.categories = data.result.categories;
    }
  };
  const handlePlannedChange = (value: string) => {
    searchInfo.selectPlannedValue = value.value;
    queryPlannedPowerData();
  };

  //=============================== 厂用电==============================================
  const factroyData = ref({});
  const factroyChartData = ref({
    barData: [[], []],
    colors: ['#488FF6', '#68E4B8'],
    names: ['下网电量', '外接厂用电量'],
    categories: [],
  });
  const queryFactroyPowerData = async () => {
    searchInfo.dimension = searchInfo.selectFactroyValue;
    let data = await getFactroyUsedPowerData(searchInfo);
    if (data.result) {
      factroyChartData.value.barData = data.result.barData;
      factroyChartData.value.categories = data.result.categories;
    }
  };
  const handleFactroyChange = (value: string) => {
    searchInfo.selectFactroyValue = value.value;
    queryFactroyPowerData();
  };

  //=============================== 储能电量==============================================
  const sbarchartSeriesName = [['充电电量', '放电电量'], ['指标']];
  const lurkingData = [
    { value: 353.22, label: '充电电量(万kWh)' },
    { value: 205.22, label: '放电电量(万kWh)' },
  ];

  // 储能电量
  const sbarchartData = {
    barData: [
      [50.0, 34.9, 74.0, 44.2, 55.6, 76.7, 35.6, 62.2, 32.6, 20.0, 62.4, 32.3],
      [32.6, 53.9, 39.0, 56.4, 48.7, 50.7, 75.6, 31.2, 48.7, 39.8, 62.0, 52.3],
    ],
    lineData: [[32.0, 39.2, 13.3, 24.5, 32.3, 15.2, 20.3, 23.4, 23.0, 16.5, 12.0, 26.2]],
    colors: [
      ['#488FF6', '#60D1F3'],
      ['#078C5D', '#68E4B8'],
      ['#E59837', '#FAE895'],
      ['#e59837', '#fae895'],
      ['#078C5D', '#68E4B8'],
    ],
    categories: [
      '1月',
      '2月',
      '3月',
      '4月',
      '5月',
      '6月',
      '7月',
      '8月',
      '9月',
      '10月',
      '11月',
      '12月',
    ],
  };

  function setValueByTopIndex() {
    //计划电量
    plannedData.value = [
      { label: '减线损上网电量(万kWh)', value: indexData.subOnline },
      { label: '计划电量(万kWh)', value: indexData.planned },
      { label: '完成率(%)', value: indexData.comRate },
    ];
    //厂用电
    factroyData.value = [
      { label: '下网电量(万kWh)', value: indexData.offline },
      { label: '网购-电量(万kWh)', value: indexData.fac },
    ];
  }

  //=============================== 数据穿透==============================================
  function handlePowerContrastByParams(params) {
    handleRouteChange('/bi/elec/powerContrast', {
      stationId: searchInfo.selectFactroyValue === 'station' ? params.data.id : '',
      orgId: searchInfo.selectFactroyValue === 'company' ? params.data.id : '',
    });
  }

  function handlePowerContrast() {
    handleRouteChange('/bi/elec/powerContrast', {});
  }

  function handlePlanPowerByParams(params) {
    handleRouteChange('/bi/elec/planned', {
      stationId: searchInfo.selectPlannedValue === 'station' ? params.data.id : '',
      orgId: searchInfo.selectPlannedValue === 'company' ? params.data.id : '',
    });
  }

  function handlePlanPower() {
    handleRouteChange('/bi/elec/planned', {});
  }

  // 更新组件函数
  const handleUpdate = () => {
    instanceKey.value = buildUUID();
  };

  defineExpose({
    handleUpdate,
  });
</script>

<style lang="less" scoped>
  .right-content {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 25vw;
    height: 100%;
    padding: 0 0.68vw 1.68vh 0.57vw;

    .section-box {
      flex: 1;
      width: 100%;
      height: 33.3333%;
      overflow: hidden;

      .section-bg1 {
        width: 100%;
        height: 100%;
        background: url('../../../../assets/images/da/section_bg_1.png') no-repeat center/100% 100%;
      }

      .section-bg2 {
        width: 100%;
        height: 100%;
        background: url('../../../../assets/images/da/section_bg_3.png') no-repeat center/100% 100%;
      }
    }

    .top-section {
      padding-bottom: 0.62vh;
    }

    .middel-section {
      padding: 0.31vh 0;
    }

    .bottom-section {
      padding-top: 0.62vh;
    }

    .fix-indicator-group-padding {
      padding-top: 1.2vh;
    }
  }
</style>
